import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { connectableObservableDescriptor } from 'rxjs/internal/observable/ConnectableObservable';
import {ClientService} from '../services/ClientService/client.service';
import { LoginInput } from '../services/models/LoginInput';
import { AuthService } from './auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  email= "";
  password= "";
  form: FormGroup;
  constructor(
    private fb: FormBuilder,
    private authService: AuthService,
    private router:Router,
    private _clientService: ClientService
  ) { 
    this.form = this.fb.group({
      email: ['',Validators.required],
      password: ['',Validators.required]
    });
  }

  ngOnInit(): void {
    
  }

  onSubmit(form:any) {
   console.log(form)
    
    if(!form.controls.email.touched && !form.controls.password.touched ) {
     console.log(' Por favor insira senha e e-mail válidos ')
    }
    
    if (form.invalid) {
      form.controls.email.markAsTouched();
      form.controls.password.markAsTouched();
    return;
    }
    
    // this.login();
    
  }

  // logar (){
  //   return this.router.navigate(['feed']);
  // }
  logar(form:any){
   
    if (form.controls.email && form.controls.password) {
        this.authService.login(form.controls.email, form.controls.password)
            .subscribe(
                () => {
                    console.log("User is logged in");
                    this.router.navigateByUrl('feed');
                }
            );
    }
}
  
  exibeErro(nomeControle: string, form:NgForm) {
    if (!form.controls[nomeControle]) {
      return false;
    }
    
    return form.controls[nomeControle].invalid && form.controls[nomeControle].touched;
    
  }

  login(form:any){
    var varEmail =form.controls.email;
    var varPassword =form.controls.password;
    var loginIput = new LoginInput(varEmail, varPassword);
    var data = JSON.stringify(loginIput);

    this._clientService
      .InsertLogin(JSON.parse(data))
      .subscribe(
      (response: any) => {
            alert("Dados inseridos com sucesso");
            this.logar(form);
      },
      (error: any) => {
        alert("Erro ao inserir Herois");
      })
  }

  

}
